@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
  margin: 0;
  padding: 0;
  background-image: url('/public/image/bg.png');
  background-size: cover;
  background-repeat: no-repeat;
  font-family: "맑은 고딕";

  line-height: 1.5;
  font-weight: bolder;
  margin-left: 12.5px;
}

header {
  text-align: center;
  font-size: 62.5px;
  margin-bottom: 12.5px;

}

#index {
  font-size: 37.5px;
  margin-left: 125px;
}

.container {
  display: grid;
  grid-template-columns: 112.5px 1fr;
}

nav {
  font-family: "맑은 고딕";
  margin-left: 25px;
  font-size: 30px;
}

main {
  font-family: "맑은 고딕";
  margin-left: 125px;
  font-size: 17.5px;
}

footer {
  margin-top: 200px;
  text-align: center;

  color: white;
  font-size: 25px;
}

a:link,
a:visited {
  background-color: #eeeeee;
  color: black;
  padding: 2.5px 5px;
  text-decoration: none;
  display: inline-block;
  margin: 10px;
  width: 112.5px;
  cursor: pointer;
  transition-duration: 0.25s;
  font-size: 1em;
  border: none;
  border-radius: 2.5px;
  box-shadow: 2.5px 2.5px 2.5px 2.5px gray;
}

a:hover,
a:active {
  background-color: green;
  color: white;
}

button {
  font-family: "굴림";
  background-color: #eeeeee;
  color: black;
  padding: 5px 5px;
  margin: 10px;
  font-size: 2em;
  cursor: pointer;
  transition-duration: 0.25s;
  border: none;
  border-radius: 2.5px;
  box-shadow: 2.5px 2.5px 2.5px 2.5px gray;
}

button:hover {
  background-color: green;
  color: white;
}
